.GoBack {
    cursor: pointer;
    width: 130px;
    height: 30px;
    background-color: rgb(50, 234, 234);
    border-radius: 5px;
    border: 1px solid rgb(36, 62, 62);
}

.Card {
    margin-top: 20px;
    border: 1px solid black;
    border-radius: 4px;
    background-color: bisque;
    width: 300px;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.butt {
    background-color: rgb(50, 234, 234); 
    border-radius: 5px;
    border: 1px solid rgb(36, 62, 62);   
}